body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app-header {
  background-color: #95EB00;
  color: #fff950;
}

.app-footer {
  background-color: #eee;
}

.app-title {
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.4);
}

.header-link,
.header-link:link,
.header-link.btn-link,
.header-link.btn-link:link {
  color: #fff;
  font-weight: bold;
}

.header-link:hover,
.header-link.btn-link:hover,
.header-link:focus,
.header-link.btn-link:focus {
  color: #fff;
  text-decoration: underline;
}

.btn-link,
a,
a:link {
  color: #FF8A01;
}

a:visited {
  color: #FF9A08;
}

a:hover,
a:focus,
a:active,
.btn-link:hover,
.btn-link:focus {
  color: #6AE83f;
}

.link-gray:hover,
.link-gray:focus {
  color: #FF8A01 !important;
}

#root,
.app-wrapper {
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  -webkit-flex-direction: column;
          flex-direction: column;
}

main {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.show-on-hover {
  display: none;
}

.word-list-item:hover .show-on-hover {
  display: inline-block;
}

.filter-item {
  padding-top: 4px;
  padding-bottom: 4px;
}

.filter-item.selected {
  background-color: #FFF115;
  color: #000;
}

.btn-link.filter-item:hover,
.btn-link.filter-item:focus {
  color: #000;
}

